import React, { useState, useEffect } from 'react';


import { withRouter } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';


import queryString from 'query-string'

import withNav from '../../hoc/Nav';

import './NewAppointment.scss';


const useStyles = makeStyles(theme => ({
    formControl: {
        width: '100%',
    },
    inputRoot: {

        '& label': {
            color: '#BEBEBE',
        },
        '& label.Mui-focused': {
            color: '#3D2892',
        },
        '& .MuiInput-underline:before': { //Default
            borderBottomColor: '#BEBEBE',
        },
        '& .MuiInput-underline:after': { //Focused
            borderBottomColor: '#3D2892',
        },

    },
    buttonRoot: {

        background: '#3D2892',
        color: 'white',
        '&:hover': {
            background: '#3D2892',
        },

    },
    buttonRoot2:{

        background: '#75C6FF',
        color: 'white',
        '&:hover': {
            background: '#75C6FF',
        },

    },
    buttonLabel: {

        textTransform: 'capitalize',
        fontSize: '1.25rem'

    },
}));

const NewAppointment = (props) => {

    const classes = useStyles();

    const [selectedProjects, setSelectedProjects] = useState([ { project:'', hours:undefined } ])

    const handleProjectChange = (e,index) => {

        e.preventDefault();

        selectedProjects[index].project = e.target.value;

        setSelectedProjects(selectedProjects);

    }

    const handleAppointmentClick = (e) => {

        e.preventDefault();

        console.log('Save appointment');

    }

    const handleAddProjectInputClick = (e) => {

        e.preventDefault();

        setSelectedProjects(selectedProjects.concat({ project:'', hours:undefined }));

    }

    const currentDate = new Date();

    const monthMap = {

        0: 'Janeiro',
        1: 'Fevereiro',
        2: 'Março',
        3: 'Abril',
        4: 'Maio',
        5: 'Junho',
        6: 'Julho',
        7: 'Agosto',
        8: 'Setembro',
        9: 'Outubro',
        10: 'Novembro',
        11: 'Dezembro'

    };

    const queryParams = queryString.parse(props.location.search);

    const monthName = monthMap[queryParams.month];

    const projects = [

        { id: 1, name: 'Plataforma AI' },
        { id: 2, name: 'Sistema de Horas' },
        { id: 3, name: 'Chatbot' }

    ];

    return (

        <div className="appointment-container" >
            <h1>{`${monthName}/${currentDate.getFullYear()}`}</h1>
            <div className="appointment-box">
                <h4>Projetos</h4>
                {
                    selectedProjects.map((sp,index) => (
                        <div className="inputs" key={`sp-${index}`} >
                            <div className="input-container">
                                <FormControl className={classes.formControl}>
                                    <InputLabel id="project-select-label">Projeto</InputLabel>
                                    <Select
                                        labelId="project-select-label"
                                        id="project-select"
                                        value={sp}
                                        onChange={(e) => handleProjectChange(e,index)}
                                        classes={{ root: classes.inputRoot }}>

                                        {
                                            projects.map(p => (
                                                <MenuItem key={`p-${p.id}`} value={p.id}>{p.name}</MenuItem>
                                            ))
                                        }

                                    </Select>
                                </FormControl>
                            </div>
                            <div className="input-container">
                                <TextField className="text-input" classes={{
                                    root: classes.inputRoot,
                                    label: classes.inputLabel,
                                }} label="Horas" />
                            </div>
                        </div>
                    ))
                }
                <div className="button-container">
                        <Button className="primary-button" classes={{
                                root: classes.buttonRoot2,
                                label: classes.buttonLabel,
                            }} variant="contained" onClick={handleAddProjectInputClick} >
                                Adicionar Projeto +
                                
                        </Button>
                </div>
                <div className="button-container">
                        <Button className="primary-button" classes={{
                                root: classes.buttonRoot,
                                label: classes.buttonLabel,
                            }} variant="contained" onClick={handleAppointmentClick} >
                                Apontar
                                <Icon>check</Icon>
                        </Button>
                </div>
            </div>
        </div>

    );

};

export default withNav(withRouter(NewAppointment));