import React, { useState } from 'react';

import { withRouter } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import TextField from '@material-ui/core/TextField';

import withNav from '../../hoc/Nav';

import './Projects.scss';

const useStyles = makeStyles({
    table: {
        minWidth: '100%',
        color: '#3D2892'
    },
    cell: {
        color: '#3D2892',
        fontWeight: 'bold',
    },
    buttonRoot: {

        background: '#3D2892',
        color: 'white',
        '&:hover': {
            background: '#3D2892',
        },

    },
    buttonLabel: {

        textTransform: 'capitalize',
        fontSize: '1.25rem'

    },
    inputRoot: {

        '& label': {
            color: '#BEBEBE',
        },
        '& label.Mui-focused': {
            color: '#3D2892',
        },
        '& .MuiInput-underline:before': { //Default
            borderBottomColor: '#BEBEBE',
        },
        '& .MuiInput-underline:after': { //Focused
            borderBottomColor: '#3D2892',
        },

    },
});

const Projects = (props) => {


    const classes = useStyles();

    const [showForm, setShowForm] = useState(false);

    const handleAddProjectInputClick = (e) => {

        setShowForm(true);

    };

    const handleSaveProjectInputClick = (e) => {

        setShowForm(false);

    };
    

    const projects = [

        { id:1, name:'Projeto X' },
        { id:2, name:'Projeto X' },
        { id:3, name:'Projeto X' },

    ];

    return (
        <div className="projects-container" >
            <h1>Projetos</h1>
            <div className="projects">
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell classes={{ root: classes.cell }} align="center">Nome</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {projects.map(row => (
                                <TableRow key={row.id} >
                                    <TableCell classes={{ root: classes.cell }} align="center">
                                        {row.name}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
            {
                showForm ?
                    <div className="form">
                        <div className="input-container">
                            <TextField className="text-input" classes={{
                                root: classes.inputRoot,
                                label: classes.inputLabel,
                            }} label="Nome" />
                        </div>
                        <div className="button-container">
                            <Button className="primary-button" classes={{
                                root: classes.buttonRoot,
                                label: classes.buttonLabel,
                            }} variant="contained" onClick={handleSaveProjectInputClick} >
                                Salvar <Icon>check</Icon>
                            </Button>
                        </div>
                    </div>
                    : <div className="button-container">
                        <Button className="primary-button" classes={{
                            root: classes.buttonRoot,
                            label: classes.buttonLabel,
                        }} variant="contained" onClick={handleAddProjectInputClick} >
                            Adicionar +
                    </Button>
                    </div>
            }
        </div>
    );

};

export default withNav(withRouter(Projects));

