import React, { useState, useEffect } from 'react';

import { withRouter } from 'react-router-dom';

import { ThemeProvider, makeStyles } from "@material-ui/styles";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';

import MonthBox from '../../shared/month-box/MonthBox';

import Auth from '../../services/AuthService';

import withNav from '../../hoc/Nav';

import './Home.scss';

const Home = (props) => {

    const auth = Auth.getInstance();

    useEffect(() => {


    }, []);

    const currentDate = new Date();

    const months = [

        'Janeiro',
        'Fevereiro',
        'Março',
        'Abril',
        'Maio',
        'Junho',
        'Julho',
        'Agosto',
        'Setembro',
        'Outubro',
        'Novembro',
        'Dezembro'

    ];

    const status = {

        0: 'ok',
        1: 'warning',
        2: 'available',
        3: 'unavailable',
        4: 'unavailable',
        5: 'unavailable',
        6: 'unavailable',
        7: 'unavailable',
        8: 'unavailable',
        9: 'unavailable',
        10: 'unavailable',
        11: 'unavailable',

    };

    const goToNewAppointment = (month,status) => {

        if(status != 'ok' && status != 'unavailable'){

            props.history.push(`/home/new-appointment?month=${month}`);

        }

    };

    return (

        <div className="home-container">
            <h1>{currentDate.getFullYear()}</h1>
            <div className="months-container">
                <div className="months">
                    {
                        months.map((month, index) => {

                            return (

                                <div key={index} onClick={() => {goToNewAppointment(index,status[index])}} >
                                    <MonthBox month={month} status={status[index]} ></MonthBox>
                                </div>
                                

                            );

                        })
                    }
                </div>
            </div>
        </div>

    );

};

export default withNav(withRouter(Home));