import React, { useEffect } from 'react';

import { withRouter, Link } from 'react-router-dom';

import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Icon from '@material-ui/core/Icon';
import MenuIcon from '@material-ui/icons/Menu';

import { makeStyles, useTheme } from '@material-ui/core/styles';

import Auth from '../../services/AuthService';

import dasaLogo from '../../assets/dasa-logo.png';

import './Drawer.scss';

const drawerWidth = 280;

const useStyles = makeStyles(theme => ({

    root: {
        display: 'flex',
    },
    drawer: {
        [theme.breakpoints.up('sm')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: `calc(100% - ${drawerWidth}px)`,
            marginLeft: drawerWidth,
        },
    },
    menuButton: {
        color:'#fff',
        top:'2rem',
        borderRadius:'0 16px 16px 0',
        position:'fixed',
        paddingLeft:'8px',
        marginLeft:0,
        marginRight: theme.spacing(2),
        backgroundColor:'#24126A',
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: drawerWidth,
        backgroundColor: '#24126A',
        color: '#fff'
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    listItemBottom: {
        position:'fixed',
        bottom:0,
    },
    listItemText:{
        fontSize:24 ,//Insert your required size
    }


}));

function ResponsiveDrawer(props) {
    const { container } = props;
    const classes = useStyles();
    const theme = useTheme();
    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    // useEffect(() => {

    //     console.log(props);

    // },[]);

    const isActiveRoute = (path) => {

        let activeRoute = props.location.pathname.slice(1);

        return activeRoute.includes(path);

    };

    const goToRoute = (path) => {

        props.history.push(path);

    }

    const auth = Auth.getInstance();

    const drawer = (
        <div>
            <div className="logo">
                <img src={dasaLogo} alt="Dasa" />
            </div>
            <div className="user-name">
                <h4>{auth.user.name}</h4>
            </div>
            <div className="nav-list">
                <List>
                    <ListItem onClick={() => goToRoute('/home')} button key={'Apontamento'}>
                        <ListItemIcon><Icon style={{ color: isActiveRoute('home') ? '#69FFBC' : "#FFF", fontSize: 24 }} >schedule</Icon></ListItemIcon>
                        <ListItemText style={{ color: isActiveRoute('home') ? '#69FFBC' : "#FFF" }} classes={{primary:classes.listItemText}} primary={'Apontamento'} />
                    </ListItem>
                    <ListItem onClick={() => goToRoute('/consultings')} button key={'Consultorias'}>
                        <ListItemIcon><Icon style={{ color: isActiveRoute('consultings') ? '#69FFBC' : "#FFF", fontSize: 24 }} >group</Icon></ListItemIcon>
                        <ListItemText style={{ color: isActiveRoute('consultings') ? '#69FFBC' : "#FFF" }} classes={{primary:classes.listItemText}} primary={'Consultorias'} />
                    </ListItem>
                    <ListItem onClick={() => goToRoute('/projects')} button key={'Projetos'}>
                        <ListItemIcon><Icon style={{ color: isActiveRoute('projects') ? '#69FFBC' : "#FFF", fontSize: 24 }} >assignment_ind</Icon></ListItemIcon>
                        <ListItemText style={{ color: isActiveRoute('projects') ? '#69FFBC' : "#FFF" }} classes={{primary:classes.listItemText}} primary={'Projetos'} />
                    </ListItem>
                    <ListItem button key={'Relatorio'}>
                        <ListItemIcon><Icon style={{ color: isActiveRoute('report') ? '#69FFBC' : "#FFF", fontSize: 24 }} >description</Icon></ListItemIcon>
                        <ListItemText style={{ color: isActiveRoute('report') ? '#69FFBC' : "#FFF" }}  classes={{primary:classes.listItemText}} primary={'Relatório'} />
                    </ListItem>
                
                    <ListItem classes={{ root:classes.listItemBottom}} onClick={() => goToRoute('/')} button key={'Logout'}>
                        <ListItemIcon><Icon style={{ color:"#FFF", fontSize: 24 }} >power_settings_new</Icon></ListItemIcon>
                        <ListItemText classes={{primary:classes.listItemText}} primary={'Sair'} />
                    </ListItem>
                </List>
            </div>
        </div>
    );

    return (
        <div className={classes.root}>
            <CssBaseline />
            <IconButton
                aria-label="open drawer"
                edge="start"
                onClick={handleDrawerToggle}
                className={classes.menuButton} >
                <MenuIcon />
            </IconButton>
            <nav className={classes.drawer} aria-label="mailbox folders">
                {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                <Hidden smUp implementation="css">
                    <Drawer
                        container={container}
                        variant="temporary"
                        anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
                <Hidden xsDown implementation="css">
                    <Drawer
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        variant="permanent"
                        open
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
            </nav>

        </div>
    );
}

ResponsiveDrawer.propTypes = {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    container: PropTypes.instanceOf(typeof Element === 'undefined' ? Object : Element),
};

export default withRouter(ResponsiveDrawer);