import React, { useState } from 'react';

import { withRouter } from 'react-router-dom';

import { ThemeProvider, makeStyles } from "@material-ui/styles";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel';
import Radio from '@material-ui/core/Radio';

import dasaLogo from '../../assets/dasa-logo.png';

import './Signup.scss';

const useStyles = makeStyles(theme => ({
    formControl: {
        width: '100%',
    },
    inputRoot: {

        '& label': {
            color: '#BEBEBE',
        },
        '& label.Mui-focused': {
            color: '#3D2892',
        },
        '& .MuiInput-underline:before': { //Default
            borderBottomColor: '#BEBEBE',
        },
        '& .MuiInput-underline:after': { //Focused
            borderBottomColor: '#3D2892',
        },

    },
    buttonRoot: {

        background: '#3D2892',
        color: 'white',
        '&:hover': {
            background: '#3D2892',
        },

    },
    buttonRoot2: {

        background: '#75C6FF',
        color: 'white',
        '&:hover': {
            background: '#75C6FF',
        },

    },
    buttonLabel: {

        textTransform: 'capitalize',
        fontSize: '1.25rem'

    },
    radio: {
        '&$checked': {
            color: '#3D2892',
        }
    },
    checked: {},
    radioLabel:{

        fontWeight:'bold',
        fontSize:'1.5rem'

    }
}));

const Signup = (props) => {

    const classes = useStyles();

    const [consulting, setConsulting] = useState('');

    const [type, setType] = useState('');

    const handleClick = (e) => {

        e.preventDefault();

        props.history.push(`/home`);


    };

    const handleConsultingChange = (e) => {

        setConsulting(consultings.filter(c => c.id == e.target.value)[0]);

    }

    const handleTypeChange = (e) => {

        setType(e.target.value);

    }

    const consultings = [
        { id: 1, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
        { id: 2, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
        { id: 3, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
    ];

    return (

        <div className="signup-container">
            <div className="logo">
                <img src={dasaLogo} alt="Dasa" />
            </div>
            <h1>Completar Cadastro</h1>
            <div className="signup-box">
                <form>
                    <div className="input-container">
                        <FormControl className={classes.formControl}>
                            <InputLabel id="consulting-select-label">Consultoria</InputLabel>
                            <Select
                                labelId="consulting-select-label"
                                id="consulting-select"
                                value={consulting}
                                onChange={handleConsultingChange}
                                classes={{ root: classes.inputRoot }}>

                                {
                                    consultings.map(c => (
                                        <MenuItem key={`c-${c.id}`} value={c.id}>{c.name}</MenuItem>
                                    ))
                                }

                            </Select>
                        </FormControl>
                    </div>
                    <div className="input-container radio-container">
                        <FormControlLabel classes={{ label:classes.radioLabel }} value="PJ" control={
                            <Radio
                                checked={type === 'PJ'}
                                onChange={handleTypeChange}
                                classes={{root: classes.radio, checked: classes.checked}}
                                value="PJ"
                                name="radio-button-demo"
                                inputProps={{ 'aria-label': 'PJ' }}
                            />
                        } label="PJ" />

                        <FormControlLabel classes={{ label:classes.radioLabel }} value="CLT" control={
                            <Radio
                                checked={type === 'CLT'}
                                onChange={handleTypeChange}
                                classes={{root: classes.radio, checked: classes.checked}}
                                value="CLT"
                                name="radio-button-demo"
                                inputProps={{ 'aria-label': 'CLT' }}
                            />
                        } label="CLT" />
                    </div>
                    <div className="input-container">
                        <TextField className="text-input" classes={{
                            root: classes.inputRoot,
                            label: classes.inputLabel,
                        }} label="Valor Hora (PJ)" />
                    </div>
                    <div className="input-container">
                        <Button className="primary-button" classes={{
                            root: classes.buttonRoot,
                            label: classes.buttonLabel,
                        }} variant="contained" onClick={handleClick} >
                            Salvar
                            <Icon>check</Icon>
                        </Button>
                    </div>
                </form>
            </div>
        </div>

    );

};

export default withRouter(Signup);
