import React, { useState } from 'react';

import { withRouter } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import TextField from '@material-ui/core/TextField';

import withNav from '../../hoc/Nav';

import './Consultings.scss';

const useStyles = makeStyles({
    table: {
        minWidth: '100%',
        color: '#3D2892'
    },
    cell: {
        color: '#3D2892',
        fontWeight: 'bold',
        width: '33.33%'
    },
    buttonRoot: {

        background: '#3D2892',
        color: 'white',
        '&:hover': {
            background: '#3D2892',
        },

    },
    buttonLabel: {

        textTransform: 'capitalize',
        fontSize: '1.25rem'

    },
    inputRoot: {

        '& label': {
            color: '#BEBEBE',
        },
        '& label.Mui-focused': {
            color: '#3D2892',
        },
        '& .MuiInput-underline:before': { //Default
            borderBottomColor: '#BEBEBE',
        },
        '& .MuiInput-underline:after': { //Focused
            borderBottomColor: '#3D2892',
        },

    },
});

const Consultings = (props) => {

    const classes = useStyles();

    const consultings = [
        { id: 1, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
        { id: 2, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
        { id: 3, name: 'Consultoria X', responsible: 'Igor Moura', email: 'consultoria@email.com' },
    ];

    const [showForm, setShowForm] = useState(false);

    const handleAddConsultingInputClick = (e) => {

        setShowForm(true);

    };

    const handleSaveConsultingInputClick = (e) => {

        setShowForm(false);

    };

    return (
        <div className="consultings-container" >
            <h1>Consultorias</h1>
            <div className="consultings">
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell classes={{ root: classes.cell }} align="center">Nome</TableCell>
                                <TableCell classes={{ root: classes.cell }} align="center">Responsável</TableCell>
                                <TableCell classes={{ root: classes.cell }} align="center">Email</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {consultings.map(row => (
                                <TableRow key={row.id} >
                                    <TableCell classes={{ root: classes.cell }} align="center">
                                        {row.name}
                                    </TableCell>
                                    <TableCell classes={{ root: classes.cell }} align="center">
                                        {row.responsible}
                                    </TableCell>
                                    <TableCell classes={{ root: classes.cell }} align="center">
                                        {row.email}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
            {
                showForm ?
                    <div className="form">
                        <div className="input-container">
                            <TextField className="text-input" classes={{
                                root: classes.inputRoot,
                                label: classes.inputLabel,
                            }} label="Nome" />
                        </div>
                        <div className="input-container">
                            <TextField className="text-input" classes={{
                                root: classes.inputRoot,
                                label: classes.inputLabel,
                            }} label="Responsável" />
                        </div>
                        <div className="input-container">
                            <TextField className="text-input" classes={{
                                root: classes.inputRoot,
                                label: classes.inputLabel,
                            }} label="Email" />
                        </div>
                        <div className="button-container">
                            <Button className="primary-button" classes={{
                                root: classes.buttonRoot,
                                label: classes.buttonLabel,
                            }} variant="contained" onClick={handleSaveConsultingInputClick} >
                                Salvar <Icon>check</Icon>
                            </Button>
                        </div>
                    </div>
                    : <div className="button-container">
                        <Button className="primary-button" classes={{
                            root: classes.buttonRoot,
                            label: classes.buttonLabel,
                        }} variant="contained" onClick={handleAddConsultingInputClick} >
                            Adicionar +
                    </Button>
                    </div>
            }
        </div>
    );

}

export default withNav(withRouter(Consultings));