import React, { useState } from 'react';

import { withRouter } from 'react-router-dom';

import { ThemeProvider, makeStyles } from "@material-ui/styles";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';

import './Login.scss';
import dasaLogo from '../../assets/dasa-logo.png';

import Auth from '../../services/AuthService';


const useStyles = makeStyles({

    buttonRoot: {

        background: '#3D2892',
        color: 'white',
        '&:hover': {
            background: '#3D2892',
        },

    },
    buttonLabel: {

        textTransform: 'capitalize',
        fontSize: '1.25rem'

    },

    inputRoot: {

        '& label': {
            color: '#BEBEBE',
        },
        '& label.Mui-focused': {
            color: '#3D2892',
        },
        '& .MuiInput-underline:before': { //Default
            borderBottomColor: '#BEBEBE',
        },
        '& .MuiInput-underline:after': { //Focused
            borderBottomColor: '#3D2892',
        },

    }

});

const Login = (props) => {

    const auth = Auth.getInstance();

    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');

    const classes = useStyles();

    function handleClick(e){

        e.preventDefault();

        auth.user = { login:'t03206015367', name:'Igor Moura' };
 

        props.history.push(`/signup`);

    }

    return (

        <div className="login-container">
            <div className="logo">
                <img src={dasaLogo} alt="Dasa" />
            </div>
            <h1>Sistema de Horas</h1>
            <div className="login-box">
                <form>
                    <div className="input-container">
                        <TextField className="text-input" classes={{
                            root: classes.inputRoot,
                            label: classes.inputLabel,
                        }} label="Login" />
                    </div>
                    <div className="input-container">
                        <TextField className="text-input" classes={{
                            root: classes.inputRoot,
                            label: classes.inputLabel,
                        }} label="Senha" />
                    </div>
                    <div className="input-container">
                        <Button className="primary-button" classes={{
                            root: classes.buttonRoot,
                            label: classes.buttonLabel,
                        }} variant="contained" onClick={handleClick} >
                            Entrar
                            <Icon>exit_to_app</Icon>
                        </Button>
                    </div>
                </form>
            </div>
        </div>

    );

}

export default withRouter(Login);